type AnnouncementRow = {
  id: number;
  created_at?: string | null;
  createdAt?: string | null;
  title_en?: string | null;
  titleEn?: string | null;
  title?: string | null;
  content_en?: string | null;
  contentEn?: string | null;
  content?: string | null;
  organization_id?: string | null;
};

async function getAnnouncements(): Promise<{ data: AnnouncementRow[]; error?: string }> {
  if (!churchOrgId) {
    return { data: [], error: "Missing NEXT_PUBLIC_CHURCH_ORG_ID" };
  }

  const supabase = createSupabaseServerClient();
  if (!supabase) {
    return { data: [], error: "Supabase keys missing" };
  }

  try {
    const { data, error } = await supabase
      .from("announcements")
      .select("*")
      .eq("organization_id", churchOrgId)
      .order("created_at", { ascending: false })
      .limit(3);

    if (error) {
      return { data: [], error: error.message };
    }

    return { data: (data ?? []) as AnnouncementRow[] };
  } catch (e) {
    return { data: [], error: e instanceof Error ? e.message : String(e) };
  }
}

